

































export default {
	data() {
		return {
			vertyText: "获取验证码",
			currentTime: 60,
			disabled: false,
			phoneNumber: '',
			password: '',
			vertyCode: '',
			invitekey: '',
			interval: null,
			showcansee: false,
			// seePassImage: '../../../static/img/viewnoicon.png',
		    piccode:'',
			focus:0
		};
	},
	computed: {
		correctPhone() {
			return this.$utils.checkMobile(this.phoneNumber);
		},
		imageurl() {
			return this.showcansee ? '../../../static/img/viewicon.png' : '../../../static/img/viewnoicon.png'
		},
		inputType(){
			return this.showcansee ?'text':'password'
		}
		
	},
	methods: {
		phoneFocus:function(){
			this.focus = 0;
		},
		vertyCodeFocus:function(){
			this.focus = 1;
		},
		recommendedFocus:function(){
			this.focus = 2;
		},
		passFocus:function(){
			this.focus = 3;
		},
		regist: function() {
			if (!this.correctPhone) {
				uni.showToast({
					title: '请输入正确手机号',
					mask: false,
					duration: 1500,
					icon:'none'
				}); 
				return;
			}
			if (this.vertyCode == '') {
				uni.showToast({
					title: '请输入验证码',
					mask: false,
					duration: 1500,
					icon:'none'
				});
				return;
			}
			if (this.password == '') {
				uni.showToast({
					title: '请输入密码',
					mask: false,
					duration: 1500,
					icon:'none'
				});
				return;
			}
			this.$utils.tRequest({
				url: this.$webUrl + "user_register",
				data: {
					mobile: this.phoneNumber,
					username: this.phoneNumber,
					password: this.password,
					code: this.vertyCode,
					invitekey: this.invitekey
				},
				methods:'POST',
				success: res => {
					// console.log()
					this.saveUserInfo(res);
				},
				fail: () => {

				},
				complete: () => {

				}
			})
		},
		seePass: function() {
			this.showcansee = !this.showcansee
		},
		getPhoneNumber: function(e) {
			this.phoneNumber = e.target.value;
			if(this.correctPhone){
				this.piccode = this.$imageUrl+'verify';
			}
		},
		getInputCode: function(e) {
			this.vertyCode = e.target.value;
		},
		getInvitekey: function() {
			this.invitekey = e.target.value;
		},
		getPassWord: function(e) {
			this.password = e.target.value;
		},
		saveUserInfo: function(res) {
			console.log(res);
			if (res.code == 0) {
				uni.showToast({
					title: res.msg,
					mask: false,
					duration: 1500,
					icon:'none',
					success: () => {
						uni.navigateBack({
							delta: 1
						});
					}
				});
			} else {
				uni.showToast({
					title: res.msg,
					mask: false,
					duration: 1500
				});
			}
		},
		clickPicVerify:function(){
			this.piccode = this.$imageUrl+'verify'+'&random='+Date.now()
		},
		getVertryCode: function() {
			var that = this;
			console.log("this.$webUrl==" + this.$webUrl);
			if (!this.correctPhone) {
				uni.showToast({
					title: '请输入正确手机号',
					mask: false,
					duration: 1500,
					icon:'none'
				});
				return;
			}
			this.$utils.tRequest({
				url: this.$webUrl + "user_verify_smscode",
				data: {
					mobile: this.phoneNumber
				},
				success: res => {
					that.AppSendVerifyCode(res);
				},
				fail: () => {

				},
				complete: () => {

				}
			})
		},
		AppSendVerifyCode(res) {
			console.log(JSON.stringify(res));
			var that = this;
			if (res.code == '0') {
				var currentTime = that.currentTime;
				this.interval = setInterval(function() {
					this.disabled = true
					currentTime--;

					that.vertyText = currentTime + '秒'
					if (currentTime <= 0) {
						clearInterval(this.interval)
						that.vertyText = '重新发送',
							that.currentTime = 60,
							that.disabled = false
					}

				}, 1000)
			} else {
				uni.showModal({
					title: '提示',
					content: '',
					showCancel: false,
					cancelText: '',
					confirmText: '知道了',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			}
		}
	}


}
